.modal {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.314);

  &__messageContainer {
    position: absolute;
    top: calc(50% - 125px);
    left: calc(50% - 125px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 250px;
    height: 250px;
    text-align: center;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 1px 2px 3px 0px rgba(155, 155, 155, 0.882);

    button {
      width: 50px;
      padding: 4px;
      margin-top: 35px;
      cursor: pointer;
      background-color: #f5f5f5;
      border: none;
      border-radius: 4px;
      box-shadow: 1px 2px 3px 0px rgba(155, 155, 155, 0.882);
    }
  }
}
