header {
  position: fixed;
  z-index: 5;
  width: 100%;
  background-color: $headerBackgroundColor;

  .menuHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px;

    &__titleCon {
      display: flex;
      align-items: center;
      color: $white;
      text-shadow: 2px 2px #ff0000;
      cursor: pointer;

      &__icon {
        width: 45px;
        height: 45px;
        margin-right: 8px;
        margin-top: 6px;
      }
    }

    &__searchSection {
      display: flex;
      align-items: center;
      margin-top: 14px;

      &__input {
        width: 220px;
        padding: 8px;
        border-radius: 4px;
        outline: none;
        border: none;
        font-size: 1.2rem;
      }

      button {
        width: 32px;
        height: 32px;
        margin-left: 8px;
        border-radius: 4px;
        border: none;
        background-color: $white;
        cursor: pointer;
        box-shadow: 1px 2px 3px 0px rgba(155, 155, 155, 0.882);
        transform: translateY(-0.1em);
        transition: transform 0.1s ease;

        img {
          margin-top: 4px;
          width: 50%;
          height: 50%;
        }
      }

      button:active {
        transform: translateY(0);
      }
    }
  }

  .menuHeader__titleCon:hover .menuHeader__titleCon__icon {
    animation: spin 0.5s linear infinite;
  }
}

@include media-up(lg) {
  header {
    .menuHeader {
      &__searchSection {
        margin-top: 0px;
      }
    }
  }
}
